html,
body {
  /* IE 10-11 didn't like using min-height */
  height: 100%;
  overflow: hidden;
}
body {
  display: flex;
  flex-direction: column;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: auto;
}

h1 {
  color: #ff6600;
}

.content {
  flex: 1 0 auto; /* Prevent Chrome, Opera, and Safari from letting these items shrink to smaller than their content's default minimum size. */
  padding: 16px;
  overflow: auto;
}
header {
  background-color: white;
  border-bottom: 4px solid #00a41a;
  padding: 10px 0 0;
  height: 100px;
}

img {
  width: 100%;
  height: 100%;
}

.menu-icon-width {
  width: 24px;
  color: white;
  margin-bottom: 2px;
  filter: invert(1);
}

.chevron-icon-width {
  width: 33px;
}

ul li {
  list-style: none;
  background-color: #00a41a;
  width: 20%;
  height: 72px;
}

li {
  display: flex;
  align-items: center;
  justify-content: center;
}

.link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  font-size: 14px;
}

footer {
  height: 72px;
  background: #00a41a;
  color: white;
  width: 100%;
  text-align: center;
  flex-shrink: 0; /* Prevent Chrome, Opera, and Safari from letting these items shrink to smaller than their content's default minimum size. */
}
